
.button {
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  padding: 0;
  height: 50px;
  width: 50px;
  border: 3px solid transparent;
  border-radius: 50%;
  background-color: transparent;
  color: var(--dark-color);
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  line-height: 0;
  -webkit-transition: border-color 300ms ease-out, -webkit-transform 300ms ease-out;
  transition: border-color 300ms ease-out, -webkit-transform 300ms ease-out;
  transition: transform 300ms ease-out, border-color 300ms ease-out;
  transition: transform 300ms ease-out, border-color 300ms ease-out, -webkit-transform 300ms ease-out;
}
.button svg {
  height: 100%;
  width:100%;
  fill: var(--accent-color);
  -webkit-transition: fill 300ms ease-out;
  transition: fill 300ms ease-out;
}
.button:focus {
  outline: none;
  border-color: var(--accent-color);
}
.active {
  -webkit-transform: rotate(45 deg);
      -ms-transform: rotate(45 deg);
          transform: rotate(45 deg);
}
.active svg {
  fill: var(--primary-color);
}
.active:focus {
  border-color: var(--primary-color);
}





